import { HttpClient ,HttpHeaders} from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Rx';
import { AlertController,LoadingController } from 'ionic-angular';

/*
  Generated class for the SettingProvider provider.
,
  "proxies": [
    {
      "path": "/app",
      "proxyUrl": "http://47.96.48.160/app"
    }
  ]
  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class SettingProvider {
   
   /*******************************************
   * 
   ******************************************/
   /* */
   heartbeat = 1000 * 30;
   /*  */
   loginstate:boolean = false;
   /**/
   user :any;
   /**/

   cityColumns: any[];
   /**/
   apiUrl :string ="http://47.96.48.160/app";
   /**/
   epidemicPrevention :Object;
   keyword :string ="";
   /**/
   scanResult = "";
   /**/
   ltockinsert:boolean = true;
   /*loading*/
   loading;
   /**/
   formatDate = ( time: any ) => {
          const Dates = new Date( time );
          const year: number = Dates.getFullYear();
          const month: any = ( Dates.getMonth() + 1 ) < 10 ? '0' + ( Dates.getMonth() + 1 ) : ( Dates.getMonth() + 1 );
          const day: any = Dates.getDate() < 10 ? '0' + Dates.getDate() : Dates.getDate();
          const hours:any = Dates.getHours();
          const min:any = Dates.getMinutes();
          const seconds:any = Dates.getSeconds();
          return year + '-' + month + '-' + day + " " + hours +":" + min + ":"+seconds;
    };
    /* */
    farmername="";
   /**/
   ltock:any = {
      code:"",
      farmId:"0",
      motherAddress:"",
      epidemicIds:"",
      slaughterAddress:"",
      slaughterRemark:"",
      saleFlow:"",
      epidemicType:"",
      epidemicProduct:"",
      epidemicBatch:"",
      healthy:1,
      insureState:2,
      type:"1",
      insureId:"1",
      accidentDead:"1",
      sex:"1",
      slaughterDate : this.formatDate( new Date().getTime() )
   };
   
    /* http */
   httpOptions:Object = {
      headers: new HttpHeaders({
        'Content-Type':  'application/json',
        })
   }; 


   /**/
   constructor(public http: HttpClient,public alertCtrl:AlertController,public loadingCtrl:LoadingController) {

   }


   


   /******************************************
   * 
   ******************************************/

   /*http loading*/
   presentLoading(title:string="..."){
    console.log("loading  ");
     this.loading = this.loadingCtrl.create({
        spinner: 'ios',
        content: title        
      });
   }

   /* http loading */
   dismissLoading(){
      console.log("loading  ");
      this.loading.dismiss();
   }
  
   /*  */
   presentAlert(msg:string,title:string,buttons:any[] = [{text :title==''?'':title}]) 
    {
      let alert = this.alertCtrl.create({
      title: '',
      subTitle: msg,
      cssClass:'alert-ios',
      buttons: buttons,
      enableBackdropDismiss:true
      });
      alert.present();
    return alert;
  }

   /*  */
   getEpidemicPrevention(areaid){
     return this.http.get(this.apiUrl+"/specialManager/epidemicPrevention/queryByArea/"+areaid,this.httpOptions);
   }

   /* heartbeat */
   getheartbeat(){
      return this.http.get("http://47.96.48.160/heartbeat/special_manager",this.httpOptions);
   }
   
}



